#s-cover {
	width: 1px;
	padding-left: 35px;
}

button {
	position: relative;
	display: block;
	width: 84px;
	height: 80px;
	cursor: pointer;
}

#s-circle {
	position: relative;
	top: -8px;
	left: 0;
	width: 43px;
	height: 43px;
	margin-top: 0;
	border-width: 15px;
	border: 15px solid #fff;
	background-color: transparent;
	border-radius: 50%;
	-webkit-transition: 0.5s ease all;
	transition: 0.5s ease all;
}

button span {
	position: absolute;
	top: 68px;
	left: 43px;
	display: block;
	width: 45px;
	height: 15px;
	background-color: transparent;
	border-radius: 10px;
	-webkit-transform: rotateZ(52deg);
	        transform: rotateZ(52deg);
	-webkit-transition: 0.5s ease all;
	transition: 0.5s ease all;
}

button span:before,
button span:after {
	content: '';
	position: absolute;
	bottom: 0;
	right: 0;
	width: 20px;
	height: 10px;
	background-color: #fff;
	border-radius: 10px;
	-webkit-transform: rotateZ(0);
	        transform: rotateZ(0);
	-webkit-transition: 0.5s ease all;
	transition: 0.5s ease all;
}

#s-cover:hover #s-circle {
	top: -1px;
	width: 67px;
	height: 15px;
	border-width: 0;
	background-color: #fff;
	border-radius: 20px;
}

#s-cover:hover span {
	top: 50%;
	left: 56px;
	width: 25px;
	margin-top: -9px;
	-webkit-transform: rotateZ(0);
	        transform: rotateZ(0);
}

#s-cover:hover button span:before {
	bottom: 11px;
	-webkit-transform: rotateZ(52deg);
	        transform: rotateZ(52deg);
}

#s-cover:hover button span:after {
	bottom: -11px;
	-webkit-transform: rotateZ(-52deg);
	        transform: rotateZ(-52deg);
}

#s-cover:hover button span:before,
#s-cover:hover button span:after {
	right: -6px;
	width: 40px;
	background-color: #fff;
}

/*Resize the wrap to see the search bar change!*/
.wrap {
	width: 30%;
	position: absolute;
	top: 50%;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
}

/*slider*/
#carouselExampleIndicators {
	position: relative;
	height: 100vh;
	width: 100%;
}

#carouselExampleIndicators .carousel-inner {
	position: relative;
	height: 100%;
	width: 100%;
	background: #000;
}

#carouselExampleIndicators .carousel-inner:before {
	position: absolute;
	content: '';
	background: rgba(0, 0, 0, 0.45);
	top: 0;
	left: 0;
	height: 100%;
	width: 100%;
	z-index: 1;
}

#carouselExampleIndicators .carousel-inner .carousel-item {
	position: relative;
	height: 100%;
	width: 100%;
	background: url('https://images.pexels.com/photos/1749057/pexels-photo-1749057.jpeg?cs=srgb&dl=when-will-you-return-signage-1749057.jpg&fm=jpg');
	background-size: 100% 100%;
	background-position: center;
	background-attachment: fixed;
}

#carouselExampleIndicators .carousel-inner .carousel-item:nth-child(2) {
	background: url('https://images.pexels.com/photos/1712/sunglasses-apple-iphone-desk.jpg?cs=srgb&dl=sunglasses-apple-iphone-desk-1712.jpg&fm=jpg');
	background-size: 100% 100%;
	background-position: center;
	background-attachment: fixed;
}

#carouselExampleIndicators .carousel-inner .carousel-item:nth-child(3) {
	background: url('https://images.pexels.com/photos/414171/pexels-photo-414171.jpeg?cs=srgb&dl=adventure-calm-clouds-dawn-414171.jpg&fm=jpg');
	background-size: 100% 100%;
	background-position: center;
	background-attachment: fixed;
}

#carouselExampleIndicators .carousel-inner .carousel-item .info {
	position: absolute;
	top: 50%;
	-webkit-transform: translateY(-50%);
	        transform: translateY(-50%);
	height: auto;
	width: 100%;
	z-index: 1;
	text-align: center;
}

#carouselExampleIndicators .carousel-inner .carousel-item h1 {
	height: auto;
	color: #fff;
	width: 100%;
	z-index: 1;
	text-align: center;
	font-size: 5.5em;
	font-weight: 750;
}

#carouselExampleIndicators .carousel-inner .carousel-item p {
	height: auto;
	color: #ccc;
	width: 100%;
	z-index: 1;
	text-align: center;
	font-size: 1.2em;
	font-weight: 500;
}
